@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h5,
p {
  line-height: 1.6em;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --background-color-main: white;
  --background-color-dark: #243447;
  --content-color-main-opacity: rgba(0, 0, 0, 0.4);
  --content-color-dark-opacity: rgba(255, 255, 255, 0.4);
  --content-color-main: black;
  --content-color-dark: white;
  --border-color-main: rgba(0, 0, 0, 0.4);
  --border-color-dark: rgba(255, 255, 255, 0.4);
  --large-title-size: 2.4em;
  --subtitle-font: 1.3em;
  --section-heading-font: 1.9em;
  --content-heading-size: 1.4em;
  --para-font: 1em;
  --small-para-font: 0.9em;
}

@media screen and (max-width: 768px) {
  :root {
    --large-title-size: 1.9em;
    --subtitle-font: 1em;
    --para-font: 0.8em;
    --section-heading-font: 1.4em;
    --content-heading-size: 1.2em;
  }
}

@media screen and (min-width: 1600px) {
}

@media screen and (min-width: 1900px) {
  :root {
    --large-title-size: 3.9em;
    --subtitle-font: 1.8em;
    --para-font: 1.4em;
    --section-heading-font: 2.4em;
    --content-heading-size: 1.8em;
  }
}

body {
  background-color: white;
  background-color: var(--background-color-main);
  transition: all 0.2s linear;
}

a {
  text-decoration: none;
  color: black;
  color: var(--content-color-main);
}

.dark a {
  color: #243447;
  color: var(--background-color-dark);
}

body.dark {
  background-color: #243447;
  background-color: var(--background-color-dark);
  transition: all 0.2s linear;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.container {
  padding-right: 5px;
  padding-left: 5px;
  margin-right: auto;
  margin-left: auto;
}

.container-fluid {
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 768px) {
  .container {
    width: 750px;
  }

  .container-fluid {
    width: 750px;
  }
}

@media (min-width: 992px) {
  .container {
    width: 970px;
  }

  .container-fluid {
    width: 970px;
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }

  .container-fluid {
    width: 1170px;
  }
}

.row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0 30px;
}

img {
  height: auto;
  object-fit: contain;
}

section {
  min-height: 90vh;
  height: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  scroll-snap-align: start;
}

.position-relation {
  position: relative;
}

.Main-banner {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.banner-title {
  font-size: var(--large-title-size);
  font-weight: 600;
  color: var(--content-color-main);
}

.dark .banner-title {
  color: var(--content-color-dark);
}

.banner-subtitle {
  margin-top: 0.7em;
  font-size: var(--subtitle-font);
  color: var(--content-color-main);
}

.dark .banner-subtitle {
  color: var(--content-color-dark);
}

.avatar {
  margin: 2.5em 0;
  transition: 2s;
  width: 0px;
  transition: width 3s;
}

.avatar-animation {
  position: relative;
}

.greeting-message {
  position: absolute;
  left: 110%;
  /* background-color: var(--background-color-dark); */
  color: var(--content-color-main);
  padding: 10px;
  /* width: 370px; */
  width: calc(100vh * 0.45);
  overflow: hidden;
  text-align: left;
  top: 30%;
  /* box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.12); */
  border-radius: 15px;
  display: none;
}

.dark .greeting-message {
  /* background-color: var(--background-color-main); */
  color: var(--content-color-dark);
}

.greeting-text {
  color: inheir;
  font-size: var(--para-font);
  overflow: hidden;
}

@media screen and (max-width: 769px) {
  .Main-banner {
    text-align: left;
  }

  .avatar-animation {
    position: relative;
    /* align-self: flex-start; */
  }

  .greeting-message {
    position: relative;
    left: 0;
    padding: 0 10px;
    /* background-color: var(--background-color-dark); */
    overflow: hidden;
    top: 0;
    /* width: 50%; */
  }
}

.about-section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;
  height: auto;
  padding: 20px 0;
  width: 100%;
  background-color: #7d89a2;
  /* background-color: var(--background-color-main) */
}

/* .dark .about-section {
  background-color: var(--background-color-dark);
} */

.about-section-para {
  font-size: var(--para-font);
  line-height: 1.7em;
  font-style: oblique;
  z-index: 9;
  color: var(--content-color-dark);
}

.box-large {
  position: absolute;
  border: solid;
  border-width: 20px;
  /* border-color: transparent transparent transparent white; */
  border-color: rgba(256, 256, 256, 0.2);
  /* border-right-color: transparent; */
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin-left: -50px;
  margin-right: auto;
  margin-bottom: auto;
  margin-top: auto;
  z-index: 6;
  width: 350px;
  height: 350px;
  transition: -webkit-transform 1s linear;
  transition: transform 1s linear;
  transition: transform 1s linear, -webkit-transform 1s linear;
}

.about-section:focus .box-large,
.about-section:hover .box-large {
  -webkit-transform: rotatez(-45deg);
          transform: rotatez(-45deg);
}

@media (max-width: 500px) {
  .box-large {
    height: 100%;
    -webkit-transform: rotatez(0deg);
            transform: rotatez(0deg);
  }
}

